import type { AddressChange, AddressDto } from "types/common/address";
import { AddressDtoTypes } from "@enums/location.enum";

export function mapAddressChangeToAddressDto(addressChange: AddressChange): AddressDto {
  if ("placeId" in addressChange) {
    return {
      address: addressChange.placeId,
      type: AddressDtoTypes.ADDRESS_STRING_DTO,
    };
  } else {
    return addressChange;
  }
}

export const defaultAddress = {
  type: AddressDtoTypes.ADDRESS_STRING_DTO,
  address: "",
};
